import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { SectionProps } from '../../utils/SectionProps';
import Timer from 'react-compound-timer'

const propTypes = {
    ...SectionProps.types,
    split: PropTypes.bool
}

const defaultProps = {
    ...SectionProps.defaults,
    split: false
}

const TimerSection = ({
    className,
    topOuterDivider,
    bottomOuterDivider,
    topDivider,
    bottomDivider,
    hasBgColor,
    invertColor,
    split,
    lotterySeconds,
    ...props
}) => {

    const outerClasses = classNames(
        'cta section center-content-mobile reveal-from-bottom',
        topOuterDivider && 'has-top-divider',
        bottomOuterDivider && 'has-bottom-divider',
        hasBgColor && 'has-bg-color',
        invertColor && 'invert-color',
        className
    );

    function miliseconds(hrs, min, sec) {
        return ((hrs * 60 * 60 + min * 60 + sec) * 1000);
    }

    let sec = lotterySeconds * 1000;
    return (
        <div className="container" style={{
            textAlign: 'center',
            paddingTop: '10px',
            paddingBottom: '10px',
            color: 'white',
            backgroundColor: '#25282c'
        }}>
            <span style={{ marginRight: '4px' }}>Next Lottery Ends In</span>
            {lotterySeconds != 0 && <Timer
                initialTime={sec}
                direction="backward"
                checkpoints={[
                    {
                        time: 60000 * 60 * 48,
                        callback: () => console.log('Checkpoint A'),
                    },
                    {
                        time: 60000 * 60 * 48 - 5000,
                        callback: () => console.log('Checkpoint B'),
                    }
                ]}
            >
                <Timer.Days style={{ marginLeft: '10px' }} /> <span style={{ marginRight: '4px', marginLeft: '1px' }} >days</span>
                <Timer.Hours style={{ marginLeft: '10px' }} /> <span style={{ marginRight: '4px', marginLeft: '1px' }} >hours</span>
                <Timer.Minutes /> <span style={{ marginRight: '4px', marginLeft: '1px' }}>minutes</span>
                <Timer.Seconds /> <span style={{ marginRight: '4px', marginLeft: '1px' }}>seconds</span>
            </Timer>}
        </div>
    );
}

TimerSection.propTypes = propTypes;
TimerSection.defaultProps = defaultProps;

export default TimerSection;