import React, { useState, useEffect } from 'react';
// import sections
import Hero from '../components/sections/Hero';
import FeaturesTiles from '../components/sections/FeaturesTiles';
import FeaturesSplit from '../components/sections/FeaturesSplit';
import Info from '../components/sections/Info';
import Testimonial from '../components/sections/Testimonial';
import Cta from '../components/sections/Cta';
import Community from '../components/sections/Community';
import Web3 from 'web3';
import ABI from '../ABI.json';
import TierABI from './TierABI.json';

const Home = () => {

  const [tier0PotSize, setTier0PotSize] = useState(0);
  const [tier1PotSize, setTier1PotSize] = useState(0);
  const [tier2PotSize, setTier2PotSize] = useState(0);
  const [tier0Holders, setTier0Holders] = useState(0);
  const [tier1Holders, setTier1Holders] = useState(0);
  const [tier2Holders, setTier2Holders] = useState(0);
  const [lotterySeconds, setLotterySeconds] = useState(0);

  const web3 = new Web3('https://bsc-dataseed.binance.org/');
  let TokenContract = new web3.eth.Contract(ABI, '0xd07cB33D1c9D75349EB72359022d7bE5D8350C0a');
  let Tier0 = new web3.eth.Contract(TierABI, '0x3D7C0190EFf1aA29305a44aC38Cf2651E37A2B25');
  let Tier1 = new web3.eth.Contract(TierABI, '0xC7a647298eB0401Dbc865e1a4df6db7F3aA3c0e2');
  let Tier2 = new web3.eth.Contract(TierABI, '0xBBCC8CaF57018Bb105BD9c3b1928BE66bbE97d0E');

  useEffect(() => {
    const fetchData = async () => {
      await window.ethereum.enable();
      // await TokenContract.methods.approve('0xd07cB33D1c9D75349EB72359022d7bE5D8350C0a', 25);
      // const jackpot = await TokenContract.methods.jackpot().call();
      const seconds = await TokenContract.methods.secondsRemaining().call();
      let tier2Pot = await Tier2.methods.jackpot().call();
      let tier1Pot = await Tier1.methods.jackpot().call();
      let tier0Pot = await Tier0.methods.jackpot().call();
      let tier0Holder = await Tier0.methods.getPlayerCount().call();
      let tier1Holder = await Tier1.methods.getPlayerCount().call();
      let tier2Holder = await Tier2.methods.getPlayerCount().call();
      // let t = await TokenContract.methods.updateAccountTicketsExternal().send({ from: '0x384214CB6b4246641434135b19856649c9f1f642' });

      // let tier0Reward = await Tier0.methods.maxRewardSize().call();
      // let tier1Reward = await Tier1.methods.maxRewardSize().call();
      // let tier2Reward = await Tier2.methods.maxRewardSize().call();

      // const maxPot = await TokenContract.methods.getMaxJackpotSizePercentage().call();
      // const totalSupply = await TokenContract.methods.totalSupply().call();
      // const minTicketBalance = await TokenContract.methods.getMinTicketBalance().call();
      // let calculatedJackpot = web3.utils.fromWei(jackpot); //If it's not already a String
      // let calculatedMaxPot = (maxPot * web3.utils.fromWei(totalSupply)) / 1000;
      // let calculatedCurrentMaxPot = calculatedJackpot > calculatedMaxPot ? calculatedMaxPot : calculatedJackpot;
      // let num = calculatedJackpot.toString();
      // num = num.slice(0, (num.indexOf(".")) + 3); //With 3 exposing the hundredths place
      // calculatedCurrentMaxPot = calculatedCurrentMaxPot.toString();
      // calculatedCurrentMaxPot = calculatedCurrentMaxPot.slice(0, (num.indexOf(".")) + 3); //With 3 exposing the hundredths place
      tier0Pot = Number(web3.utils.fromWei(tier0Pot)); //If it's not already a String
      tier1Pot = Number(web3.utils.fromWei(tier1Pot));
      tier2Pot = Number(web3.utils.fromWei(tier2Pot));
      // tier0Pot += Number(web3.utils.fromWei(tier1Pot));
      // tier0Pot += Number(web3.utils.fromWei(tier2Pot));
      setTier2PotSize(Number(tier2Pot));
      setTier1PotSize(Number(tier1Pot));
      setTier0PotSize(Number(tier0Pot));
      setTier0Holders(tier0Holder);
      setTier1Holders(tier1Holder);
      setTier2Holders(tier2Holder);
      setLotterySeconds(parseInt(seconds, 10));
      // setMaxPot(calculatedMaxPot.toLocaleString());
      // setCurrentMaxPot(Number(calculatedCurrentMaxPot).toLocaleString('en-US', { maximumFractionDigits: 2 }));
    };

    fetchData();
  }, []);

  return (
    <>
      <Hero className="illustration-section-01" />
      <Testimonial tier0PotSize={tier0PotSize} tier1Holders={tier1Holders} tier2Holders={tier2Holders} tier0Holders={tier0Holders} tier1PotSize={tier1PotSize} tier2PotSize={tier2PotSize} topDivider />
      <Info lotterySeconds={lotterySeconds} topDivider />
      <FeaturesTiles topDivider />
      <FeaturesSplit invertMobile topDivider imageFill className="illustration-section-02" />
      <Community topDivider />
      {/* <Cta split /> */}
    </>
  );
}

export default Home;